export default {
  data: () => ({
    initialTouchX: null,
    isAllow: false
    // initialTouchY: null
  }),

  created() {
    document.addEventListener('touchstart', this.chooseInitialTouch);
    document.addEventListener('touchend', this.chooseEvent);
    document.addEventListener('touchmove', this.test);
  },

  methods: {
    test(event) {
      const currentTouchX = event.changedTouches[0].pageX;
      const difference = this.initialTouchX - currentTouchX;
      if (Math.abs(difference) > 200 && !this.isAllow) {
        this.isAllow = true;
        navigator.vibrate(30);
      }

      if (Math.abs(difference) < 200) this.isAllow = false;
    },

    chooseInitialTouch(event) {
      this.initialTouchX = event.changedTouches[0].pageX;
      // this.initialTouchY = event.changedTouches[0].pageY;
    },

    chooseEvent(event) {
      const endTouchX = event.changedTouches[0].pageX;
      // const endTouchy = event.changedTouches[0].pageX;
      const difference = this.initialTouchX - endTouchX;

      if (Math.abs(difference) > 200) {
        if (difference > 0) {
          this.leftSwype();
        } else {
          this.rightSwype();
        }
      }
    }
  }
};
