<template>
  <div>
    <button
      :disabled="disabled === 'true'"
      :name="title"
      class="g-button button"
      tabindex="-1"
      @click="onClick(toPage, toUri)"
    >
      <i
        class="glyph-icon button__icon"
        :class="icon"
      />
      {{ title | localization }}
    </button>
    <div class="g-debugger">
      toUri: {{ toUri }}
      <br>
      toPage: {{ toPage }}
      <br>
      hot-key: {{ hotKey }}
      <br>
      title + name: {{ title }}
    </div>
  </div>
</template>

<script>
import localization from '@/mixins/localization';

export default {
  name: 'Button',

  props: {
    'to-uri': {
      type: String,
      default: '',
    },
    'to-page': {
      type: String,
      default: '',
    },
    'hot-key': {
      type: String,
      default: '',
    },
    'title': {
      type: String,
      default: '',
    },
    'icon': {
      type: String,
      default: 'chat_bubble_outline',
    },
    'disabled': {
      type: String,
      default: '',
    },
    'onClick': {
      type: Function,
      default: () => {}
    }
    // 'name': {
    //   type: String,
    //   default: '',
    // },
    // 'value': {
    //   type: Number,
    //   default: null,
    // },
  },

  mixins: [
    localization,
  ],
};
</script>

<style lang="scss">
.button {
  position: relative;

 &__icon {
    position: absolute;
    top: 31%;
    left: 0;
    font-weight: 400;
  }
}
</style>
