<template>
  <div>
    <div class="g-debugger">
      <button
        class="g-button"
        @click="currentPage = 'acl/menu'"
      >
        В меню
      </button>
      current page:
      <span class="j-current-page">
        {{ currentPage }}
      </span>
      <br>
      current step: {{ currentStep }}
    </div>
    <div
      v-for="(pageData, pageDataIndex) in pagesData"
      :key="pageDataIndex"
    >
      <template v-if="pageData.items">
        <div
          v-for="infoItem in pageData.items.info"
          :key="infoItem.name"
        >
          <Info
            v-if="currentStep >= pageData['current-step']"
            :name="infoItem.name"
            :value="infoItem.value"
            :step="pageData['current-step']"
          />
        </div>
      </template>
    </div>
    <div
      v-for="page in templates"
      :key="page.name"
    >
      <div v-if="page.name === currentPage">
        <Title
          v-if="page.items.title"
          :title="page.items.title"
          :is-localization="true"
        />
        <Title
          v-if="page.items.login"
          :title="page.items.login"
        />
        <template
          v-for="pageData in pagesData"
        >
          <Title
            v-if="pageData.items
              && pageData.items.number
              && pageData.name === currentPage"
            :key="pageData.name"
            :title="pageData.items.number"
          />
          <list
            v-if="page.items.list
              && pageData.name === currentPage"
            :key="pageData.name"
            :is-filter="page.items.list['is-filter']"
            :filter-uri="page.items.list['filter-uri']"
            :to-uri="page.items.list['to-uri']"
            :name="page.items.list.name"
            :items="pageData.items.list.items"
            :onSubmit="startFilter"
            :onInput="selectFilterValue"
            :onClick="selectData"
            :onDblclick="sendData"
          />
          <Table
            v-if="page.items.table
              && pageData.name === currentPage"
            :key="pageData.name"
            :table="pageData.items.table"
          />
        </template>
        <Field
          v-for="item in page.items.field"
          :key="item.label"
          :hint="item.hint"
          :name="item.name"
          :label="item.label"
          :type="item.type"
          :to-uri="item['to-uri']"
          :onInput="selectData"
          :onSubmit="sendData"
        />
        <ButtonMenu
          v-for="menuButton in page.items['btn-menu']"
          :key="menuButton.name"
          :title="menuButton.title"
          :icon="menuButton.icon"
          :to-uri="menuButton['to-uri']"
          :to-page="menuButton['to-page']"
          :counter="menuButton.counter"
          :onClick="buttonAction"
        />
        <Button
          v-for="button in page.items['btn']"
          :key="button.title"
          :title="button.title"
          :icon="button.icon"
          :to-uri="button['to-uri']"
          :to-page="button['to-page']"
          :hot-key="button['hot-key']"
          :name="button.name"
          :value="button.value"
          :onClick="buttonAction"
        />
      </div>
    </div>
  </div>
</template>

<script>
//components
import Info from '@/components/Info';
import Field from '@/components/Field';
import List from '@/components/List';
import Button from '@/components/Button';
import ButtonMenu from '@/components/ButtonMenu';
import Table from '@/components/Table';
import Title from '@/components/Title';
import Preloader from '@/components/Preloader';

//mixins
import alertMessage from '@/mixins/alertMessage';
import localization from '@/mixins/localization';

//models
import dataFields from './models/dataFields';

export default {
  name: 'Main',

  components: {
    Info,
    Field,
    List,
    Button,
    ButtonMenu,
    Table,
    Title,
    Preloader,
  },

  mixins: [
    alertMessage,
    localization
  ],

  data: () => ({
    currentPage: 'acl',
    currentStep: null,

    templates: [],
    pagesData: [],

    dataFields: dataFields(),

    soketConect: null,
  }),

  watch: {
    currentPage: function (currentPage) {
      this.pagesData.forEach((page) => {
        page.name === currentPage ? this.currentStep = page['current-step'] : null;
      });

      currentPage === 'acl/menu' ? this.pagesData = [] : null;
    },

    currentStep: function (currentStep) {
      currentStep === '0' ? this.pagesData = [] : null;
    }
  },

  created() {
    this.createWebSocket();
    document.addEventListener('keydown', this.keyDownListener);
  },

  methods: {
    keyDownListener({ key }) {
      const input = document.querySelector('.j-input');

      if (document.activeElement !== input) {
        this.templates.forEach((template) => {
          if (template.name === this.currentPage) {
            template.items.btn.forEach((button) => {
              if (button["hot-key"] === key) {
                if (button["to-page"]) {
                  this.currentPage = button['to-page'];
                  this.dataFields = dataFields();
                }
                if (button["to-uri"]) {
                  this.buttonAction(null, button['to-uri']);
                }
              }
            });
          }
        });
      }
    },

    selectFilterValue(filterUri, filterValue) {
      this.dataFields.filterUri = filterUri;
      this.dataFields.filterValue = filterValue;
    },

    selectData(data) {
      this.dataFields.data = this.dataFields.data || {};
      Object.assign(this.dataFields.data, data);
    },

    startFilter() {
      const { filterUri } = this.dataFields;
      const { filterValue } = this.dataFields;
      if (filterUri, filterValue) {
        this.webSoketSendMessage(filterUri, filterValue);
      }
    },

    sendData(toUri) {
      const { data } = this.dataFields;
      this.webSoketSendMessage(toUri, data);
    },

    buttonAction(toPage, toUri) {
      const { filterUri } = this.dataFields;
      const { filterValue } = this.dataFields;
      const { data } = this.dataFields;

      if (!toUri) {
        this.currentPage = toPage;
        this.dataFields = dataFields();
      } else if (data) {
        this.sendData(toUri);
      } else if (filterUri, filterValue) {
        this.startFilter();
      } else {
        this.sendData(toUri);
      }
    },

    //webSocket
    createWebSocket() {
      const  WS_PROTOCOL = 'ws://';
      const  WS_HOST = '194.0.150.90';
      const  WS_PORT = '11188';
      const  WS_STR_CONNECT = WS_PROTOCOL + WS_HOST + ':' + WS_PORT;
      const  socket = new WebSocket(WS_STR_CONNECT);
      this.soketConect = socket;

      socket.onopen = this.onOpenWebSocket;
      socket.onclose = this.onCloseWebSocket;
      socket.onmessage = this.onSendWebSocket;
      socket.onerror = this.onErrorWebSocket;
    },

    webSoketSendMessage(uri, data, params) {
      this.soketConect.send(JSON.stringify({
        uri,
        data,
        params,
      }));
    },

    //load menu + pages
    onOpenWebSocket() {
      console.log('Соединение установлено.');
        this.webSoketSendMessage('/acl/');
        this.webSoketSendMessage('/acl/load/');
    },

    onCloseWebSocket(event) {
      if (event.wasClean) {
        console.log('Соединение закрыто чисто');
      } else {
        console.log('Обрыв соединения'); // например, "убит" процесс сервера
      }
      console.log('Код: ' + event.code + ' причина: ' + event.reason);

      //При закрытии сокета алерт о обрыве забит хардом!!!!!!!
      this.createAlertMessage('Обрыв соединения!', 'error');
    },

    onSendWebSocket(event) {
      // console.log('Получены данные ' + event.data);
      this.dataProcessing(event.data)
    },

    onErrorWebSocket(error) {
      console.log('Ошибка ' + error.message);
    },

    dataProcessing(data) {
      const parsedData = JSON.parse(data);
      console.log(parsedData);

      if (parsedData['show-page']) {
        let pagesData = [];
        pagesData = this.pagesData.filter((page) => page.name !== parsedData['show-page'].name);
        pagesData.push(parsedData['show-page']);
        this.pagesData = pagesData;

        this.currentPage = parsedData['show-page'].name;
        this.currentStep = parsedData['show-page']['current-step'] || this.currentStep;
        this.dataFields = dataFields();

        if (parsedData['show-page'].message) {
          this.createAlertMessage(parsedData['show-page'].message);
        }
      }

      if (parsedData['set-page']) {
        const notChangedTemplates = this.templates.filter((template) => {
          const reduce = parsedData['set-page'].reduce((acc, newTemplate) => {
            let isEqual = acc;
            if (template.name === newTemplate.name) {
              isEqual += 1;
            }
            return isEqual;
          }, 0);
          if (reduce === 0) {
            return template;
          };
        });

        this.templates = [...notChangedTemplates, ...parsedData['set-page']];
      }

      if (parsedData.error) {
        this.createAlertMessage(parsedData.error, 'error');
      }
    },
  },
};
</script>
