<template>
  <div class="field">
    <form @submit.prevent="onSubmit(toUri)">
      <label>
        <h3
          v-if="label"
          class="field__label"
        >
          {{ label | localization }}:
        </h3 >
        <input
          :type="type"
          :name="name"
          :placeholder="hint | localization"
          class="g-input j-input"
          v-model="fieldValue"
          @input="
            (name === 'password' || name === 'login')
              ?  onInput( { [name]: fieldValue })
              :  onInput( { val: fieldValue })
          "
        >
      </label>
      <div class="g-debugger">
        toUri: {{ toUri }}
        name: {{ name }}
        type: {{ type }}
      </div>
    </form>
  </div>
</template>

<script>
import localization from '@/mixins/localization';

export default {
  name: 'Field',

  data: () => ({
    fieldValue: '',
  }),

  props: {
    'to-uri': {
      type: String,
      default: '',
    },
    'hint': {
      type: String,
      default: '',
    },
    'type': {
      type: String,
      default: '',
    },
    'label': {
      type: String,
      default: '',
    },
    'name': {
      type: String,
      default: '',
    },
    'onSubmit': {
      type: Function,
      default: () => {},
    },
    'onInput': {
      type: Function,
      default: () => {},
    },
    // 'value': {
    //   type: String,
    //   default: '',
    // },
    // 'label-hint': {
    //   type: String,
    //   default: '',
    // },
  },

  mixins: [
    localization,
  ],
};
</script>

<style lang="scss" scoped>
.field {
  &__label {
    margin: 0;
  }
}
</style>

