<template>
  <div
    class="radio-button"
    :class="{ 'radio-button--active': isActive }"
    @click="onClick"
    @dblclick="onDblclick"
  >
    <div
      class="radio-button__flag"

    />
    <div class="radio-button__text">
      {{ text }}
    </div>
  </div>
</template>

<script>
import localization from '@/mixins/localization';

export default {
  name: 'RadioButton',

  props: {
    text: {
      type: String,
      default: '',
    },
    isActive: {
      type: Boolean,
      default: false ,
    },
    onClick: {
      type: Function,
      default: () => {}
    },
    onDblclick: {
      type: Function,
      default: () => {}
    },
  },

  mixins: [
    localization,
  ],
};
</script>

<style lang="scss">
@import '~@/global/scss/variables';

.radio-button {
  cursor: pointer;
  position: relative;
  padding: 1em;
  margin: 0.2em 0;
  border: 0;
  border-left: 8px solid $in-active;
  background: $elements-background-color;
  outline: none;

  &::before {
    content: "";
    position: absolute;
    left: -8px;
    top: 50%;
    bottom: 50%;
    opacity: 0;
    width: 8px;
    background: $main-color;
    transition: all 0.3s ease;
  }

  &--active {
    &::before {
      top: 0;
      bottom: 0;
      opacity: 1;
    }
  }
}
</style>
