<template>
  <div>
    <button
      :name="title"
      class="g-button button-menu"
      tabindex="-1"
      @click="onClick(toPage, toUri)"
      :title="title | localization"
      :disabled="!counter"
    >
      <i
        class="glyph-icon button-menu__icon"
        :class="icon"
      />
      <span>
        {{ title | localization }}
      </span>
      <span
        v-if="counter > 0"
        class="button-menu__counter"
      >
        ({{ counter }})
      </span>
    </button>
    <div class="g-debugger">
      title + name: {{ title }}
      <br>
      toUri: {{ toUri }}
      <br>
      toPage: {{ toPage }}
    </div>
    <br>
  </div>
</template>

<script>
import localization from '@/mixins/localization';

export default {
  name: 'ButtonMenu',

  props: {
    'counter': {
      type: Number,
      default: null
    },

    'to-uri': {
      type: String,
      default: ''
    },
    'to-page': {
      type: String,
      default: ""
    },
    'title': {
      type: String,
      default: ""
    },
    'icon': {
      type: String,
      default: "fingerprint"
    },
    'onClick': {
      type: Function,
      default: () => {}
    }
    // 'name': {
    //   type: String,
    //   default: ''
    // },
    // 'value': {
    //   type: String,
    //   default: ''
    // },
    // 'disabled': {
    //   type: String,
    //   default: 'false',
    // },
  },

  mixins: [localization]
};
</script>

<style lang="scss">
@import "~@/global/scss/variables";

.button-menu {
  position: relative;

  &__icon {
    position: absolute;
    top: 31%;
    left: 0;
    font-weight: 400;
  }

  &__counter {
    position: absolute;
    top: 30%;
    right: 15px;
  }
}
</style>
