<template>
  <h3 class="title">
    <span v-if="isLocalization">
      {{ title | localization}}
    </span>
    <span v-else >
      {{ title }}
    </span>
  </h3>
</template>

<script>
import localization from '@/mixins/localization';

export default {
  name: 'Title',

  props: {
    title: {
      type: String,
      default: '',
    },
    isLocalization: {
      type: Boolean,
      default: false,
    },
  },

  mixins: [localization],
};
</script>

<style lang="scss">
.title {
  text-align: center;
  margin: 5px;
}
</style>
