<template>
  <div class="list">
    <form @submit.prevent="onSubmit">
      <input
        v-if="isFilter"
        type="text"
        class="g-input g-input--filter j-input"
        v-model="filterValue"
        @input="onInput(filterUri, { val: filterValue })"
      >
      <div class="g-debugger">
        filterUri: {{ filterUri }}
      </div>
    </form>
    <div v-if="items.length > 0">
      <RadioButton
        v-for="item in items"
        :key="item.value"
        :text="item.name"
        :is-active="selectedItem === item.value"
        :onClick="(() => {
          selectedItem = item.value,
          onClick({ val: item.value })
        })
        "
        :onDblclick="(() => onDblclick(toUri))"
      />
    </div>
  </div>
</template>

<script>
import RadioButton from './components/RadioButton';

export default {
  name: 'List',

  components: {
    RadioButton,
  },

  data: () => ({
    selectedItem: '',
    filterValue: '',
  }),

  props: {
    'is-filter': {
      type: Boolean,
      default: false,
    },
    'to-uri': {
      type: String,
      default: '',
    },
    'filter-uri': {
      type: String,
      default: '',
    },
    'items': {
      type: Array,
      default: () => ([]),
    },
    'name': {
      type: String,
      default: '',
    },
    'onSubmit': {
      type: Function,
      default: () => {},
    },
    'onInput': {
      type: Function,
      default: () => {},
    },
    'onClick': {
      type: Function,
      default: () => {},
    },
    'onDblclick': {
      type: Function,
      default: () => {},
    },
    // 'value': {
    //   type: String,
    //   default: '',
    // },
    // 'title': {
    //   type: String,
    //   default: '',
    // },
  },
};
</script>

<style lang="scss">
.list {
  text-align: center;
  width: 100%;
  padding: 1em 0.1em;
  margin: .2em 0;
  max-height: 25em;
  overflow: auto;
}
</style>
